
	import {setOpenIdStorage} from '@/test/tool.js'
export default {
	onLaunch: function() {
		console.log('App Launch');
	},
	onShow: function() {
		console.log('App Show');
		const that = this;

		//获取openid
		this.getUserInfo();
		uni.getSetting({
			success(res) {
				console.log('地图位置');
				uni.hideLoading();
				if (!res.authSetting['scope.userLocation']) {
					uni.authorize({
						scope: 'scope.userLocation',
						success() {
							that.chooseLocation();
							return;
						},
		
						fail() {
							uni.showToast({
								title: '无法获取地图权限',
								duration: 2000,
								icon:"none"
							});
						}
					});
				} else {
					that.chooseLocation();
					return;
				}
			}
		});
		

	},
	onHide: function() {
		console.log('App Hide');   
	},
	methods:{
		chooseLocation(){
			const that = this;
			uni.getLocation({
			    type: 'wgs84',
			    success: function (res) {
			        // console.log(res);
			        // console.log('当前位置的经度：' + res.longitude);
			        // console.log('当前位置的纬度：' + res.latitude);
						
					var upData={
						lost_address:'只提供经纬度',
						lost_longitude:res.longitude,
						lost_latitude:res.latitude
					}
					that.$http
						.post('/interface/rest/http/xlwb/xlgc-wb-xcx-hqdydwxx.htm', upData, { params: {} })
						.then(res => {
							console.log("获取用户经纬度提交");
							console.log(res);
							if (res.data.msgState == 1) {
								// uni.showToast({
								// 	title: res.data.msg,
								// 	duration: 2000
								// });
								
							}else{
								uni.showToast({
									title: res.data.msg,
									duration: 2000,
									icon:"none"
								});
							}
							
						})
						.catch(err => {
							console.log(err);
							uni.showToast({
								title:"获取地理位置失败",
								duration: 2000,
								icon:"none"
							});
						});
			    }
			});
		},
		getUserInfo(){
			console.log('获取用户信息')
		var 	that =this
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				   console.log('code')
			    console.log(loginRes.code)
				var upData={
					code:loginRes.code,
					
				}
				that.$http
					.post('/interface/rest/http/platform_general/getOpenId.htm', upData, { params: {} })
					.then(res => {
						console.log("获取oppenid");
						console.log(res);
						if (res.data.msgState == 1) {
							var openId=res.data.result
							setOpenIdStorage(openId)
						}else{
							uni.showToast({
								title: res.data.msg,
								duration: 2000,
								icon:"none"
							});
						}
						
					})
					.catch(err => {
						console.log(err);
						uni.showToast({
							title:"获取地理位置失败",
							duration: 2000,
							icon:"none"
						});
					});
			    // 获取用户信息
			    uni.getUserInfo({
			      provider: 'weixin',
			      success: function (infoRes) {
					   console.log('2222222');
					  console.log(infoRes.userInfo);
			        console.log('用户昵称为：' + infoRes.userInfo.nickName);
			      }
			    });
			  }
			});
		}
	
	}
};
